
  <template>
  <el-card class="my-login"
           v-bind="$attrs"
           :style="styles"
           :class="classes">
    <div v-if="logo" class="my-login__logo">
      <slot name="logo">
        <img :src="logo" alt="">
      </slot>
    </div>
    <div v-if="title" class="my-login__title">
      <slot name="title">{{title}}</slot>
    </div>
    <my-form class="my-login__form"
             ref="form"
             :rules="rules"
             @submit="handleSubmit"
             :footer="null">
      <my-input :props="{prefixIcon:'el-icon-user'}"
                class="my-login__input"
                :disabled="loading"
                :name="keyMap.account"
                :placeholder="accountPlaceholder"></my-input>
      <my-input :props="{prefixIcon:'el-icon-lock',type:'password'}"
                class="my-login__input"
                :disabled="loading"
                :name="keyMap.password"
                :placeholder="passwordPlaceholder"></my-input>
      <div v-if="$slots.extra" class="my-login__extra">
        <slot name="extra"></slot>
      </div>
    </my-form>

    <el-row class="my-login__action" :gutter="10">
      <el-col :span="actionSpan">
        <el-button type="primary" :loading="loading" @click="submit"></el-button>
      </el-col>
      <el-col v-if="pki" :span="actionSpan">
        <el-button type="success" class="my-login__pki" @click="handlePki">PKI</el-button>
      </el-col>
    </el-row>
    <slot name="footer"></slot>
  </el-card>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Card as __Card__,Col as __Col__,Row as __Row__} from 'element-ui';
     import {MyForm as __MyForm__,MyInput as __MyInput__} from '$ui';
     
     
     
     /**
   * 
   * @module $ui/components/my-login
   */
  /**
   * 
   * @member slots
   * @property {string} logo logo
   * @property {string} title 
   * @property {string} extra 
   * @property {string} footer 
   */
  export default {
    name: 'MyLogin',
    /**
     * ,  el-card 
     * @member props
     * @property {string} [logo] logo
     * @property {string} [title] 
     * @property {boolean} [pki] pki
     * @property {boolean} [transparent] 
     * @property {string} [width=400px] 
     * @property {object} [keyMap]  {account,password}
     * @property {function} [login] Promise
     * @property {boolean} [center] 
     * @property {string} [accountPlaceholder=]
     * @property {string} [passwordPlaceholder=]
     */
    props: {
      logo: {
        type: String,
        default() {
          return require('$ui/assets/xdh.png')
        }
      },
      title: {
        type: String,
        default: ''
      },
      // pki
      pki: Boolean,
      // 
      transparent: Boolean,

      width: {
        type: String,
        default: '400px'
      },
      keyMap: {
        type: Object,
        default() {
          return {
            account: 'account',
            password: 'password'
          }
        }
      },
      login: Function,
      //  
      center: Boolean,
      accountPlaceholder: {
        type: String,
        default: ''
      },
      passwordPlaceholder: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        loading: false
      }
    },
    computed: {
      rules() {
        const {account, password} = this.keyMap
        return {
          [account]: [{
            required: true,
            message: ''
          }],
          [password]: [{
            required: true,
            message: ''
          }]
        }
      },
      styles() {
        return {
          width: this.width
        }
      },
      classes() {
        return {
          'is-transparent': this.transparent,
          'is-center': this.center
        }
      },
      actionSpan() {
        return this.pki ? 12 : 24
      }
    },
    methods: {
      handleSubmit(model) {
        if (this.login) {
          this.loading = true
          this.login(model)
            .then(res => {
              this.$message.success('')
              /**
               * 
               * @event success
               * @param res 
               */
              this.$emit('success', res)
              return res
            })
            .catch(e => {
              this.$message.error(e.message || e.msg || '')
              /**
               * 
               * @event success
               * @param e 
               */
              this.$emit('error', e)
            }).finally(_ => {
            this.loading = false
          })
        }
        /**
         * 
         * @event submit
         * @param model 
         */
        this.$emit('submit', model)
      },
      handlePki() {
        /**
         * PKI
         * @event pki
         */
        this.$emit('pki')
      },
      /**
       * 
       * @method submit
       */
      submit() {
        this.$refs.form.submit()
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Card__);
__vue__.use(__Col__);
__vue__.use(__Row__)
     __vue__.use(__MyForm__);
__vue__.use(__MyInput__)
    
  </script>


  